.headroom{margin-left:150px;background-color:#FFFBF1}.headroom .row{width:100%;margin:0 auto;display:flex;justify-content:space-evenly}.headroom a{font-family:'Montserrat';font-size:large;color:black;text-decoration:none;position:relative;top:20%}.headroom a:hover{color:#556798}.headroom--unfixed{position:relative;transform:translateY(0);height:35px}.headroom--scrolled{position:relative;transform:translateY(0);transition:transform 200ms ease-in-out;height:35px}.headroom--unpinned{position:fixed;transform:translateY(-100%)}.headroom--pinned{position:fixed;transform:translateY(0%)}.sidebar{height:100%;width:150px;position:fixed;z-index:2;left:0;top:5px;padding-top:40px;background-color:#FFFBF1}.sidebar img{border-radius:150px;margin-left:12px;width:125px;border:2px solid #418DA7}.sidebar ol{list-style-type:none}.sidebar div.myName{font-family:'Montserrat';margin-left:40px;font-size:larger;font-weight:600}.sidebar li{font-family:'Montserrat'}.sidebar li:not(:last-child){margin-bottom:10px}.sidebar li.grow{transition:all .2s ease-in-out}.sidebar li.grow:hover{transform:scale(1.1)}.my-masonry-grid{display:flex;margin-left:-30px;width:auto}.my-masonry-grid_column{padding-left:30px;background-clip:padding-box}.my-masonry-grid_column>div{margin-bottom:30px;font-family:'Playfair Display'}.content{margin-left:160px;margin-top:50px}.body{background-color:#FFFBF1}

